import React, { useEffect, useRef, useState } from "react";
import './Header.css';
import { Sidebar, Menu, Icon } from "semantic-ui-react";

const Header = () => {
  const [visible, setVisible] = useState(false);

  const toggleSidebar = () => {
    setVisible(!visible);
  };


  return (
    <div>
      <nav className="avenir menu flex-item">
        <div className="menu">
          <a
            className="black pa3"
            style={{ textDecoration: 'none', color: 'inherit' }}
            href="/"
          >
            Prigodnaya.com
          </a>
        </div>
        <div>
          <img
            className="pa3 "
            alt="sidebar"
            src="menu.png"
            onClick={toggleSidebar}
          ></img>
        </div>
      </nav>
      {visible && (
        <Sidebar
        as={Menu}
        animation='overlay'
        icon='labeled'
        inverted
        onHide={() => setVisible(false)}
        vertical
        visible={visible}
        width='thin'
      >
        <Menu.Item as='a'>
          <Icon name='home' />
          Home
        </Menu.Item>
        <Menu.Item as='a'>
          <Icon name='gamepad' />
          Games
        </Menu.Item>
        <Menu.Item as='a'>
          <Icon name='camera' />
          Channels
        </Menu.Item>
      </Sidebar>
      )}
    </div>
  );
};

export default Header;
